body{
background-color: #bc1010 !important; 
background-color:#def;
background-image: -webkit-radial-gradient(closest-side, transparent 98%, rgba(171,15,11) 99%),
-webkit-radial-gradient(closest-side, transparent 98%, rgba(171,15,11) 99%);
background-image: -o-radial-gradient(closest-side, transparent 98%, rgba(171,15,11) 99%),
-o-radial-gradient(closest-side, transparent 98%, rgba(171,15,11) 99%);
background-image: radial-gradient(closest-side, transparent 98%, rgba(171,15,11) 99%),
radial-gradient(closest-side, transparent 98%, rgba(171,15,11) 99%);
background-size:80px 80px;
background-position:0 0, 40px 40px;
}

.app-container{
  position: relative;
  text-align: center;
  margin: auto; 
}

.title-container{
  position: relative;
  width:100%;
  /*margin-left: 12%;*/
  text-align: center;
  font-size: 3vw;
  font-family: Impact, Charcoal, sans-serif;
}

.oval-table-container{

  position: relative;
  margin: auto;
  height: 700px;
  width: 1000px;
  background-color: #228B22;
  border-radius: 50%;
}

.app-container{
  display: inline-block;
  text-align: center; 
  width: 100%; 
}

.button-container{
position: absolute;
   margin: auto;
   top: 0;
   left: 0;
   bottom: 0;
   right: 0;
   width: 30%;
   height: 25%;
   font-family: Impact,Charcoal,sans-serif;
   font-size: 1.2vw;
   border-radius: 2px;
}

.card-image-container{
  display: inline-block;
  text-align: center;
}

.card-image-container img {
  width: 60px;
  padding-bottom: 10px;
  padding-right: 3px;
  padding-top: 5px;
}

.player-A-container{
  position: absolute;
  -webkit-transform: rotate(90deg);
      -ms-transform: rotate(90deg);
          transform: rotate(90deg);
  left: 107px;
    top: 300px;
} 

.player-B-container{
    position: absolute;
    margin-left: auto;
    margin-right: auto;
    left: 0;
    right: 0;
    top: 100px;
} 

.player-C-container{
  position: absolute;
  -webkit-transform: rotate(-90deg);
      -ms-transform: rotate(-90deg);
          transform: rotate(-90deg);
  right: 107px;
    top: 300px;
}  

.player-name-container{
  font-size: 20px;
  font-family: Sans-serif;
  font-weight: bold}

.player-score-container{
      font-size: 25px;
}


